.login-page {
  padding-top: 10vh;
  max-width: 300px;
  margin: 0 auto;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.login-title {
  line-height: 1.5;
  font-size: 24px;
  font-weight: 300;
}

.login-message {
  width: 450px;
  background-color: #ECF6FE;
  border: 1px solid #B1DFF3;
  border-radius: 2px;
  color: rgba(0, 0, 0, 0.87);
}
