.badge {
  display: inline-block;
  padding: 4px;
  background-color: #e6e6e6;
  border-radius: 3px;
  font-size: 12px;
  font-weight: 600;
  color: #333;
  text-transform: uppercase;
  white-space: nowrap;
  line-height: 8px;
}
.badge:empty {
  display: none;
}

a.badge:hover,
a.badge:focus,
a.badge:active {
  text-decoration: underline;
}
a.badge {
  border-bottom: none;
}

.list-group-item-heading > .badge {
  float: right;
  margin: 3px;
}
.list-group-item-heading > .badge + .badge {
  margin-right: 5px;
}

.badge-info {
  background-color: #d9edf7;
  color: #0e516f;
}

.badge-success {
  background-color: #dff0d8;
  color: #215821;
}

.badge-warning {
  background-color: #fcf8e3;
  color: #6f4f17;
}

.badge-error {
  background-color: #f2dede;
  color: #862422;
}

.counter-badge {
  color: #FFFFFF;
  background-color: #2E7CB5;
  font-size: 12px;
  padding: 0.3em 0.8em;
  border-radius: 1em;
}

.counter-badge:empty {
  display: none;
}
