.marketplace-editions {
  display: flex;
  flex-direction: row;
  margin-left: -8px;
  margin-right: -8px;
}

.marketplace-edition {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 8px;
  margin-right: 8px;
  max-width: 50%;
}

.marketplace-edition .markdown img {
  width: 16px;
}

.marketplace-edition .markdown h3 {
  font-size: 14px;
  margin-top: 0;
}

.marketplace-edition-badge {
  position: absolute;
  right: -1px;
  top: 16px;
  padding: 4px 8px;
  border-radius: 2px 0 0 2px;
  height: inherit;
  line-height: inherit;
}

.marketplace-edition-action {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.marketplace-plugin-table {
  table-layout: fixed;
  width: 100%;
}

.marketplace-plugin-license {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
