.code-snippet {
  background: #e6e6e6;
  border-radius: 3px;
}

.code-snippet pre {
  padding: 8px 16px;
  border-right: 1px solid rgba(200, 200, 200, 0.5);
  overflow-y: hidden;
  overflow-x: auto;
}

.code-snippet > button {
  height: auto;
  border: 0;
  border-radius: 0;
  background: transparent;
  padding: 8px;
}

.code-snippet > button:hover,
.code-snippet > button:focus,
.code-snippet > button:active {
  background-color: rgba(200, 200, 200, 0.5);
  color: #236a97;
}
